      renderDefaultTable() {
        const $this = this;

        this.otherTable = window.$("#others-insurance").DataTable({
          ordering: true,
          paging: false,
          searching: false,
          columnDefs: [{ targets: "no-sort", orderable: false }]
        });

        window
          .$("#others-insurance tbody")
          .on("click", "a.btn-collapse", function () {
            let tr = window.$(this).closest("tr");
            let row = $this.otherTable.row(tr);
            let index = row.index();
            let child = $this.insurance.others[index].child;

            if (row.child.isShown()) {
              window.$(this).removeClass("is-open");
              row.child.hide();
              tr.removeClass("shown");
            } else {
              window.$(this).addClass("is-open");
              row
                .child($this.renderOthersChild(child, "#others-insurance"))
                .show();
              tr.addClass("shown");
            }
          });

        window
          .$("#others-insurance tbody")
          .on("click", "a.sub-other-insure-edit", function () {
            $this.isInsureFormEdit = true;
            $this.insureFormEditType = true;
            $this.selectedInsureType = 6;
