      dateFormatRemoveHMS(value) {
        if (!value || value === "") return "— —";
        let dt = window.moment(value);
        let m = dt.month() + 1 < 10 ? `0${dt.month() + 1}` : dt.month() + 1;
        let d = dt.date();
        return `${dt.year() - 1911}/${m}/${d}`;
