      otherInsuranceClassified(policylist) {
        policylist.forEach((item, index) => {
          if (item.insurancetype) {
            var contractName = null;
            switch (item.insurancetype) {
              case 1: //車險
                switch (item.contract_type) {
                  case 0: //汽車險(強制險)
                    contractName = "汽車險(強制險)";
                    break;
                  case 1: //汽車險(任意險)
                    contractName = "汽車險(任意險)";
                    break;
                  case 2: //機車險(強制險)
                    contractName = "機車險(強制險)";
                    break;
                  case 3: //機車險(附加傷害險)
                    contractName = "機車險(附加傷害險)";
                    break;
                  case 4: //機車險(任意險)
                    contractName = "機車險(任意險)";
                    break;
                  case 5: //汽車險(附加傷害險)
                    contractName = "汽車險(附加傷害險)";
                    break;
                  default:
                    contractName = item.insurancetype;
                }
                var ins1 = {
                  no: item.plcno,
                  company: item.compnm,
                  name: contractName,
                  client: item.cname,
                  type: contractName,
                  startAt: item.rdstdt,
                  years: item.prd,
                  fee: item.prem,
                  buildYear: item.target_birth
                };
                this.insurance.otherCar.push(ins1);
                break;
              case 2: //火險
                switch (item.contract_type) {
                  case 0: //住宅火險
                    contractName = "住宅火險";
                    break;
                  case 1: //商業火險
                    contractName = "商業火險";
                    break;
                  case 2: //住家綜合保險
                    contractName = "住家綜合保險";
                    break;
                  case 3: //地震保險
                    contractName = "地震保險";
                    break;
                  default:
                    contractName = item.insurancetype;
                }
                var ins2 = {
                  no: item.plcno,
                  company: item.compnm,
                  name: contractName,
                  client: item.cname,
                  type: contractName,
                  startAt: item.rdstdt,
                  years: item.prd,
                  fee: item.prem,
                  buildYear: item.target_birth
                };
                this.insurance.otherFire.push(ins2);
                break;
              case 3: //新種險
                switch (item.contract_type) {
                  case 0: //個人傷害險
                    contractName = "個人傷害險";
                    break;
                  case 1: //團體傷害險
                    contractName = "團體傷害險";
