                    break;
                  default:
                    contractName = item.insurancetype;
                }
                var ins3 = {
                  no: item.plcno,
                  company: item.compnm,
                  name: contractName,
                  client: item.cname,
                  startAt: item.rdstdt,
                  years: item.prd,
                  fee: item.famt,
                  money: item.prem
                };
                this.insurance.otherNewInsure.push(ins3);
                break;
              default:
                break;
            }
          }
        });
      },
      initCustomerDetail4() {
        //查詢國泰壽險
        this.insuranceLoading = true
        getCustInsurance(
          this.$store.state.userInfo.id,
          this.$store.state.custInfo.ID
        ).then(result => {
          let mainIns = [];
          let secondaryIns = [];
          if(result.body.policies) {
          result.body.policies.forEach((item, index) => {
            let bankName = "查無資料";
            if (item.bank_name && item.bank_name.trim() != "") {
              bankName = item.bank_name;
            }
            if (!item.pay_kind) {
              item.pay_kind = "--";
            }
            let uncheckIns = {
              no: item.policy_no, //保單號碼
              name: item.prod_nm, //險別名稱
              clientId: item.apc_id, //要保人ID
              client: item.apc_name, //要保人姓名
              target_id: item.insd_id, //被保人ID
              target: item.insd_name, //被保人姓名
              years: item.pay_prd, //年期
              startAt: this.dateFormatRemoveHMS(item.issue_date), //投保始期
              fee: item.bre_prem || '', //契約保費
              type: item.face_amt || '', //保額/計劃別
              typecurr: item.face_amt_unit, //保額/計劃別
              sum: item.cul_prem  || '', //保單總繳
              pay: `${item.pay_freq}/${item.pay_kind}`, //繳別 //繳費方式
              currency: !item.cul_prem || item.cul_prem == '' ? '' : item.curr, //保費加總
              bank: bankName, //扣款銀行名稱
              isInvIns: item.isINV_PROD, //是否為投資型保單
              account_no: item.account_no || '', //扣款帳號/信用卡後四碼
              fet_code: item.fet_code, //契約效力
              is_inv_prod : item.isINV_PROD, //是否為投資型商品
              status: 1,
              prod_id: item.prod_id, //險別代號,
              fet_name: item.fet_name,
              child: []
            };
            switch (item.prod_cat) { //
              case "1": //主約
                mainIns.push(uncheckIns);
                break;
              case "2": //副約
                secondaryIns.push(uncheckIns);
                break;
              default:
                break;
            }
          })}
          secondaryIns.forEach((item1, index1) => {
            mainIns.forEach((item2, index2) => {
              if (item1.no === item2.no) {
                item2.child.push(item1);
              }
            });
          });
          mainIns.forEach((item, index) => {
            if (item.child.length > 0) {
              item.child.sort((a, b) => {
                return a.prod_id > b.prod_id ? 1 : -1;
              });
            }
          });
          this.insurance.cathay = mainIns;
          
          this.insuranceComplete[0] = 1
          this.checkInsLoading()
        });

        //查詢國泰產險-團險
        getCustInsuranceGroup(
          this.$store.state.userInfo.id,
          this.$store.state.custInfo.ID
        ).then(result => {
          if (result.body && result.body.detail) {
            this.insurance.group = result.body.detail.map(item => {
              return {
