          const mainIns = [];
          const secondaryIns = [];
          if(result.body.policies && result.body.policies.contracts) {
          result.body.policies.contracts.forEach((item, index) => {
            let beneficiaryArr = [];
            if (
              typeof item.bnf_death_nm != "undefined" &&
              item.bnf_death_nm.length > 0
            ) {
              //身故受益人
              beneficiaryArr.push(`身故: ${item.bnf_death_nm}`);
            }
            if (
              typeof item.bnf_matag_nm != "undefined" &&
              item.bnf_matag_nm.length > 0
            ) {
              //滿期金受益人
              beneficiaryArr.push(`滿期: ${item.bnf_matag_nm}`);
            }
            if (
              typeof item.bnf_annuity_nm != "undefined" &&
              item.bnf_annuity_nm.length > 0
            ) {
              //年金受益人
              beneficiaryArr.push(`年金: ${item.bnf_annuity_nm}`);
            }
            if (
              typeof item.bnf_birth_nm != "undefined" &&
              item.bnf_birth_nm.length > 0
            ) {
              //祝壽金受益人
              beneficiaryArr.push(`祝壽: ${item.bnf_birth_nm}`);
            }
            const uncheckIns = {
              company: item.companyname,
              no: result.body.policies.plcno,
              name: item.productname,
              client: result.body.policies.apcnm,
              target: item.name,
              years: item.prd,
              startAt: item.issue_date,
              fee: item.prem,
              type: item.sche_amt,
              sum: item.famt,
              pay: item.pay_freq,
              currency: item.currencytype,
              bank: "合作金庫5041",
              beneficiary: beneficiaryArr,
              child: []
            };
            switch (item.contract_type) {
              case "主約":
                mainIns.push(uncheckIns);
                break;
              case "附約":
                secondaryIns.push(uncheckIns);
                break;
              default:
                break;
            }
          })}
          secondaryIns.forEach(item1 => {
            mainIns.forEach(item2 => {
              if (item1.no === item2.no) {
                item2.child.push(item1);
              }
            })
          })
          this.insurance.others = mainIns;
          this.insuranceComplete[9] = 1
          this.checkInsLoading()
        });

        //查詢同業產險-新種險
