    beforeMount() {
      let routeNumber = this.$route.name.split(/-/g).reverse();
      this.currentTab = Number(routeNumber[0]);
    },
    async mounted() {
      const insurance = {
        cathay: [],
        others: [],
        group: [],
        accident: [],
        health: [],
        harm: [],
        cathayNewInsure: [],
        otherNewInsure: [],
        travel: [],
        cathayCar: [],
        cathayFire: [],
        otherCar: [],
        otherFire: []
      };

      this.insurance = insurance;

      this.initCustomerDetail4();

      if (this.insurance) {
        for (var i in this.insurance) {
          this.emptyInsurance = !this.insurance[i].length > 1;
        }
      }

      this.renderDefaultTable();
      // }, 600)
      // this.renderDefaultTable();
      this.$forceUpdate();
