<template>
  <div class="modal fade" :class="{'is-open': open}">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <a @click="$emit('close')" class="close">
            <i class="icon-close"></i>
          </a>
          <h4 class="modal-title">預覽窗格</h4>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <div class="radio radio-inline">
              <input type="radio" v-model="printType" id="print-type-1" value="1">
              <label for="print-type-1">投資績效</label>
            </div>
          </div>
          <div id="pdf-container" class="pdf-preview"
          :class="{
            'section-loading': loading_1
          }">
            <iframe id="printPdf" :src="pdfContent"></iframe>
          </div>
          <div class="action text-right">
            <a class="btn btn-default" v-on:click="openPDF()" :disabled="loading_1">開啟PDF</a>
            <a class="btn btn-primary" @click="printStuff" :disabled="loading_1">列印</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getInvestPDF } from '../../service';
import pdf from 'pdfvuer';
import { log } from 'util';

export default {
  name: 'PrintModal',
  components:{
    pdf
  },
  props: {
    open: {
      type: Boolean,
      default: false
    },
    custs: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      loading_1: false,
      printType: 1,
      pdfContent: ''
    }
  },
  watch: {
    custs : 'initPrintModal',
    open : 'initPrintModal',
    printType : 'initPrintModal'
  },
  methods: {
    initPrintModal() {
      this.loading_1 = true
      
      let reqObj = {
        "data":{
          "POLICY_NO": "665566556", 
          "PROD_NAME": "5555", 
          "ISSUE_DATE": "2019-03-04", 
          "APC_NM": "DDDD",
          "INSD_NM": "ABC", 
          "FACE_AMT": "45456", 
          "PAY_FREQ_NM": "X", 
          "PAY_TIMES": "4555", 
          "RD_PREM": "54656", 
          "ACNT_NO_TRN_ORI": "445646", 
          "POL_ACC_VAL": "5546", 
          "cryAmtWithoutFee": "5000", 
          "BOU_DIV_L": "2000", 
          "SUM_PAY_PREM": "1000", 
          "SUM_PREM_FEE": "1000", 
          "SUM_TOT_FEE": "1000", 
          "CURR_C": "元",
          "ReturnOfInvestment": "15.2%", 
          "ReturnOfPremium": "60.2%", 
          "InvestProfit":"50.0%",
          "record1Data1": {"2019-03-02": [
            {"FUND_ID":"AA", "FUND_C_NAME":"BB", "INV_RATE":"10%"},
            {"FUND_ID":"BB", "FUND_C_NAME":"BB", "INV_RATE":"15%"}
            ]}, 
          "record1Data2": {"2019-03-03": [
            {"FUND_ID":"AA_A", "FUND_C_NAME":"AA_A", "INV_RATE":"5%", "ADD_RATIO":"5%"},
            {"FUND_ID":"AA_B", "FUND_C_NAME":"AA_B", "INV_RATE":"10%", "ADD_RATIO":"5%"},
            {"FUND_ID":"AA_C", "FUND_C_NAME":"AA_C", "INV_RATE":"15%", "ADD_RATIO":"5%"},
            ]},
          "record1Data3": {"EFFECTIVE_DATE":"2019-03-04", "AFT_AMT":"2500.00"}
        }, 
        
      }
      
      getInvestPDF(this.$store.state.userInfo.id, reqObj).then(result => {
        let file = new Blob([result], {type: "application/pdf"})
        let fileURL = window.URL.createObjectURL(file);
        this.pdfContent = fileURL
        this.loading_1 = false
      })
    },
    openPDF() {
        window.open(this.pdfContent);
    },
    printStuff(){
      let frm = document.getElementById('printPdf').contentWindow;
      frm.focus();
      frm.print();
      return false;
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-body{
  iframe{
    width: 100%;
    height: 100%;
    min-height: 500px;
  }
}
.modal.fade .pdf-preview{
  height: initial;
}
</style>
