            <div class="table-group">
              <table class="table insurance-green">
                <thead>
                  <tr>
                    <th>險種名稱/險種代號</th>
                    <th>類別</th>
                    <th>受益人姓名</th>
                    <th>關係</th>
                    <th class="text-right">受益比例</th>
                    <th>順位</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, i) in beneficiariesList"
                  :key="`record1-Data8-${i}`">
                    <td>{{item.prod_id_ch}}({{item.prod_id}})</td>
                    <td>{{item.bene_type_ch}}</td>
                    <td>{{item.bene_name}}</td>
                    <td>{{item.rltn_ch}}</td>
                    <td class="text-right">{{item.bene_rato}}</td>
                    <td>{{item.bene_prio}}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>

    <print-modal
    :open="printModalOpen"
    @close="panelClose('printModalOpen')" />
  </div>
