import { mapActions, mapGetters } from 'vuex';
import { commonMixin } from '../../mixins/common';
import { queryMainPolicyInfo } from '../../service';
import { queryMinorInsurDetail } from '../../service';
import { getInsuDetailPayRecords } from '../../service';
import { queryMainPolicyBeneficiariesInfo } from '../../service';
