import { queryInvestProfit } from '../../service'
import PrintModal from '../components/PrintInventInsuranceModal'
import { queryPaymentMessage } from '../../service'

export default {
  name: 'InventInsuranceModal',
  mixins: [commonMixin],
  props: {
    open: {
      type: Boolean,
      default: false
    },
    no: {
      type: String,
      default: ''
    },
    insuranceName: {
      type: String,
      default: ''
    },
    prodId: {
      type: String,
      default: ''
    },
    targetId: {
      type: String,
      default: ''
    }
  },
  components: { PrintModal },
  data () {
    return {
      isLoading_1: false,
      isLoading_2: false,
      isLoading_3: false,
      isLoading_4: false,
      isLoading_5: false,
      isLoading_6: false,
      isLoading_7: false,
      printModalOpen: false,
      insuranceContent: 0,
      roleOpt: ['配偶', '子女'],
      currentIndex: null,
      currentMember: null,
      printTarget: {},
      familyMembers: [
        {
