          birthday: '80/03/15',
          edit: false
        }
      ],
      insurDetailList: [],
      mainPolicyInfo: {},
      payRecordsList: [],
      beneficiariesList: [],
      investmentPerformance: {},
      investmentTargets: {},
      interestDatas: [],
      investmentSetting: [],
      record1Data0:[],
      record1Data1:[],
      record1Data1KeyDate: '',
      record1Data2:[],
      record1Data2KeyDate: '',
      // record1Data3:{},
      record1Data3KeyDate: '',
      record1Data3AFT_AMT: '',
      record1Data4:[],
      record1Data4KeyDate: '',
      record1Data5:[],
