      record1Data8:[],
      record1Data8KeyDate: '',
      profitSettingsData: [],
      profitSettingsKeyDate: '',
      PaymentDetail: [],
      PaymentRecord: []
    }
  },
  computed: {
    limitedInterestDatas: function() {
      return this.interestDatas.slice(0, 3)
    },
    getPrintTarget(){
      return $('.invent-insurance');
    }
  },
  watch: {
    'open': 'initData'
  },
  methods: {
    ...mapActions(['setPageLock']),
    initData(val) {
      if(val) {
        this.getMainPolicyInfo()
        this.getSecondaryPolicyDetail()
        this.getPayRecordsList()
        this.getBeneficiariesList()
        this.getPerformance()
        this.getProfit()
        this.getPaymentMessage()
      }
    },
    getMainPolicyInfo() {
      this.isLoading_1 = true
      queryMainPolicyInfo(this.no, this.$store.state.userInfo.id).then(result => {
        this.mainPolicyInfo = result.body;
        // console.log("查詢主約基本資料 API: "+JSON.stringify(this.mainPolicyInfo));
        if(!this.mainPolicyInfo) {
          this.mainPolicyInfo = {}
        }
        else{
          switch(this.mainPolicyInfo.MED_EXAM_CH){
            case('無體'):
              this.mainPolicyInfo.MED_EXAM_CH = '無體檢'
            break;
          }
        }
        this.isLoading_1 = false
      })
    },
    getSecondaryPolicyDetail() {
      this.isLoading_2 = true
      queryMinorInsurDetail(this.$store.state.userInfo.id, this.no).then(result => {
        if(result.body.rider_list && result.body.rider_list.length > 0) {
          this.insurDetailList = result.body.rider_list.map(item => {
            let rdAmtUnit = ''
            switch(item.rd_amt_unit) {
              case '1':
                rdAmtUnit = '元'
                break;
              case '2':
                rdAmtUnit = '百'
                break;
              case '3':
                rdAmtUnit = '千'
                break;
              case '4':
                rdAmtUnit = '萬'
                break;
              case '7':
                rdAmtUnit = '單位'
                break;
              case '8':
                rdAmtUnit = '計畫別'
                break;
              default:
                rdAmtUnit = ''
            }
            return {
              policy_no: item.policy_no,
              cs_name: item.cs_name,
              cs_id: item.id,
              eft_code_ch: item.eft_code_ch,
              term_date: item.term_date,
              rd_name: item.rd_name,
              rd_id: item.rd_id,
              rd_prem: Number(item.rd_prem),
              rd_prem_unit: item.rd_prem_unit,
              rd_amt: Number(item.rd_amt),
              rdAmtUnit: rdAmtUnit,
              curr: item.curr,
              product_link: item.product_link
            }
          })
          this.isLoading_2 = false
        } else {
          this.insurDetailList = []
          this.isLoading_2 = false
        }
      })
    },
    getPayRecordsList() {
      //查詢保全訊息-給付紀錄
      this.isLoading_4 = true
      getInsuDetailPayRecords(this.$store.state.userInfo.id, this.no).then(result => {
        if(result.body.PAY_LIST) {
          this.payRecordsList = result.body.PAY_LIST
        }
        this.isLoading_4 = false
      })
    },

    getPaymentMessage(){
      //查詢繳費訊息
      this.isLoading_3 = true
      queryPaymentMessage(this.$store.state.userInfo.id, this.no).then(result =>{
        if(result.body.PaymentDetail){
          this.PaymentDetail = result.body.PaymentDetail
        }
        if(result.body.PaymentRecord){
          this.PaymentRecord = result.body.PaymentRecord
        }
        this.isLoading_3 = false
      })
    },

    
    getBeneficiariesList() {
      //查詢主約受益人明細
      this.isLoading_5 = true
      queryMainPolicyBeneficiariesInfo(this.$store.state.userInfo.id, this.no).then(result => {
        if(result.body && result.body.rider_list) {
          this.beneficiariesList = result.body.rider_list
        } else {
          this.beneficiariesList = []
        }
        this.isLoading_5 = false
      })
    },
    getPerformance() {
      //查詢投資績效
      this.isLoading_6 = true
      queryInvestPerformance(this.$store.state.userInfo.id, this.no).then(result => {
        if(result.body && result.body.performance) {
          this.investmentPerformance = result.body.performance
        } else {
          this.investmentPerformance = {}
        }
        if(result.body && result.body.targets) {
          this.investmentTargets = result.body.targets
          let pattern = new RegExp('^NB+|^NJ+')
          if(pattern.test(this.prodId)) {
            this.investmentTargets.forEach(item => {
              item.cost = item.NET_IN_AMT,
              item.remuneration = item.REWARD_RATE
            })
          } else {
            this.investmentTargets.forEach(item => {
              item.cost = item.HOLD_COST,
              item.remuneration = item.FUND_ROI
            })
          }
        } else {
          this.investmentTargets = []
        }
        if(result.body && result.body.datas) {
          this.interestDatas = result.body.datas
        } else {
          this.interestDatas = []
        }
        this.isLoading_6 = false
      })
    },
    getProfit() {
      //查詢投資約定
      // console.log("targetId---------"+ this.targetId);
      this.isLoading_7 = true
      queryInvestProfit(this.$store.state.userInfo.id, this.targetId, this.no).then(result =>{
        if(result.body && result.body.profitSettings){
          this.profitSettingsKeyDate = result.body.profitSettings.dataList.keyDate
          this.profitSettingsData = result.body.profitSettings.dataList.B.map(item =>{
            return {
              keyDate: result.body.profitSettings.dataList.keyDate,
              FUND_CURR_NAME: item.FUND_CURR_NAME,
              PRFT_PT: item.PRFT_PT,
              QRY_YEAR: item.QRY_YEAR,
              SERIAL_NO: item.SERIAL_NO,
              DATA_KIND: item.DATA_KIND,
              FUND_C_NAME: item.FUND_C_NAME,
              PROG_ID: item.PROG_ID,
              EFT_DATE: item.EFT_DATE,
              FUND_ID: item.FUND_ID,
              INPUT_DATE: item.INPUT_DATE,
            }
          })
        }
        if(result.body && result.body.investmentSetting){
          
          if(result.body.investmentSetting.record1Data1){
            this.record1Data1KeyDate = result.body.investmentSetting.record1Data1.keyDate
            this.record1Data1 = result.body.investmentSetting.record1Data1.dataList.map(item =>{
              return {
                keyDate: result.body.investmentSetting.record1Data1.keyDate,
                FUND_ID: item.FUND_ID,
                FUND_C_NAME: item.FUND_C_NAME,
                FUND_CURR_NAME: item.FUND_CURR_NAME,
                INV_RATE: item.INV_RATE
              }      
            })
          }
          if(result.body.investmentSetting.record1Data2){
            this.record1Data2KeyDate = result.body.investmentSetting.record1Data2.keyDate
            this.record1Data2 = result.body.investmentSetting.record1Data2.dataList.map(item =>{
              return {
                keyDate: result.body.investmentSetting.record1Data2.keyDate,
                FUND_ID: item.FUND_ID,
                FUND_C_NAME: item.FUND_C_NAME,
                FUND_CURR_NAME: item.FUND_CURR_NAME,
                INV_RATE: item.INV_RATE,
                ADD_RATIO: item.ADD_RATIO
              }      
            })
          }
          if(result.body.investmentSetting.record1Data3){
            this.record1Data3KeyDate = result.body.investmentSetting.record1Data3.keyDate
            this.record1Data3AFT_AMT = result.body.investmentSetting.record1Data3.dataList[0].AFT_AMT
            // this.record1Data3 = result.body.investmentSetting.record1Data3.dataList.map(item =>{
            //   return {
            //     keyDate: result.body.investmentSetting.record1Data3.keyDate,
            //     AFT_AMT: item.AFT_AMT
            //   }      
            // })
          }
          if(result.body.investmentSetting.record1Data4){
            this.record1Data4KeyDate = result.body.investmentSetting.record1Data4.keyDate
            this.record1Data4 = result.body.investmentSetting.record1Data4.dataList.map(item =>{
              return {
                keyDate: result.body.investmentSetting.record1Data4.keyDate,
                FUND_CURR_NAME: item.FUND_CURR_NAME
              }      
            })
          }
          if(result.body.investmentSetting.record1Data5){
            this.record1Data5KeyDate = result.body.investmentSetting.record1Data5.keyDate
            this.record1Data5AFT_BOU_TYPE_OPT_C = result.body.investmentSetting.record1Data5.dataList[0].AFT_BOU_TYPE_OPT_C
            console.log("///////////////////"+this.record1Data5AFT_BOU_TYPE_OPT_C)
            // this.record1Data5 = result.body.investmentSetting.record1Data5.dataList.map(item =>{
            //   return {
            //     keyDate: result.body.investmentSetting.record1Data5.keyDate,
            //     AFT_BOU_TYPE_OPT_C: item.AFT_BOU_TYPE_OPT_C
            //   }      
            // })
          }
          if(result.body.investmentSetting.record1Data6){
            this.record1Data6KeyDate = result.body.investmentSetting.record1Data6.keyDate
            this.record1Data6 = result.body.investmentSetting.record1Data6.dataList.map(item =>{
            return {
              keyDate: result.body.investmentSetting.record1Data6.keyDate,
              FUND_CURR_NAME: item.FUND_CURR_NAME
              }      
            })
          }
          if(result.body.investmentSetting.record1Data7){
            this.record1Data7KeyDate = result.body.investmentSetting.record1Data7.keyDate
            this.record1Data7 = result.body.investmentSetting.record1Data7.dataList.map(item =>{
              return {
                keyDate: result.body.investmentSetting.record1Data7.keyDate,
                FUND_ID: item.FUND_ID,
                FUND_C_NAME: item.FUND_C_NAME,
                FUND_CURR_NAME: item.FUND_CURR_NAME,
                INV_RATE: item.INV_RATE
              }      
            })
          }
          if(result.body.investmentSetting.record1Data8){
            this.record1Data8KeyDate = result.body.investmentSetting.record1Data8.keyDate
            this.record1Data8 = result.body.investmentSetting.record1Data8.dataList.map(item =>{
              return {
                keyDate: result.body.investmentSetting.record1Data8.keyDate,
                FUND_ID: item.FUND_ID,
                FUND_C_NAME: item.FUND_C_NAME
              }      
            })
          }
          // this.investmentSetting = result.body.investmentSetting.map(item =>{
          //   return {
              
          //   }
          // })
        }
        this.isLoading_7 = false
      })
      
      
    }
    // toggleEdit(member, i) {
    //   this.currentIndex = i
    //   this.currentMember = member
    //   member.edit = true
    // },
    // removeMember (member, index) {
    //   this.currentIndex = null
    //   this.currentMember = null
    //   member.edit = false
    //   this.familyMembers.splice(index, 1)
    // },
    // updateMember (member) {
    //   member.edit = false
    //   this.currentIndex = null
    //   this.currentMember = null
    // },
    // addFamily () {
    //   if(this.currentIndex !== null) {
    //     this.familyMembers[this.currentIndex].edit = false
    //     this.currentMember = null
    //   }
    //   this.familyMembers.push({
    //     name: '',
    //     role: '',
    //     identityID: '',
    //     birthday: '',
    //     edit: false
    //   })

    //   this.familyMembers[this.familyMembers.length - 1].edit = true
