<template>
    <div>
        <h4 class="mt-0 mb-3">{{tableTitle}}</h4>
        <div class="insurance-row row">
            <table
            :id="prefix" class="table datatable"
            :class="options.color==='green' ? 'insurance-green' : 'insurance-blue'">
                <thead>
                    <tr>
                        <th
                        v-for="(item, index) in options.columns"
                        :key="`table-tr-${index}`"
                        :width="(index+1===options.columns.length ? item.width + 17 : item.width) || '*'"
                        :class="{
                            'sorting': item.sorting,
                            'sorting_asc': item.sorting && order.index==index && order.asc,
                            'sorting_desc': item.sorting && order.index==index && !order.asc,
                            'text-right': item.align && item.align === 'right'
                        }"
                        @click="columnSorting(index)"
                        >
                            <span v-html="item.label"></span>
                        </th>
                        <th class="no-sort" width="30" v-if="!editMode">
                            展開<br>附約
                        </th>
                        <th class="no-sort" width="30" v-else>編輯</th>
                    </tr>
                </thead>
                <tbody
                v-for="(insure, i) in tableData" :key="`${prefix}-${i}`"
                :id="`${prefix}-tr-${i}`"
                >
                    <tr>
                        <td
                        v-for="(col, cid) in options.columns"
                        :class="{
                            'text-right': col.align && col.align === 'right'
                        }"
                        :width="col.width || '*'"
                        :key="`${prefix}-col-${cid}`">
                            <span v-if="prefix==='cathay-insure' && col.name==='no'">
                                <a @click="col.click(insure, cid)" class="link text-underline">{{insure.no}}</a>
                                <br>
                                {{insure.name}}
                            </span>
                            <span v-else-if="prefix==='other-insure' && col.name==='no'">
                                <small>{{insure.company}} / {{insure.no}}</small>
                                <br>
                                {{insure.name}}
                            </span>
                            <span v-else-if="col.template" v-html="col.template(insure)"></span>
                            <span v-else-if="col.click && prefix==='cathay-insure' && col.name==='target'">
                                <a v-if="insure.target_id && insure.target_id!==''" @click="col.click(insure, cid)" class="link text-underline">{{insure[col.name]}}</a>
                                <span v-else>{{insure[col.name]}}</span>
                            </span>
                            <span v-else-if="col.click && prefix==='cathay-insure' && col.name==='client'">
                                <a v-if="insure.clientId && insure.clientId!==''" @click="col.click(insure, cid)" class="link text-underline">{{insure[col.name]}}</a>
                                <span v-else>{{insure[col.name]}}</span>
                            </span>
                            <span v-else-if="col.click && prefix==='other-insure' && col.name==='target'">
                                <a v-if="insure.target_id && insure.target_id!==''" @click="col.click(insure, cid)" class="link text-underline">{{insure[col.name]}}</a>
                                <span v-else>{{insure[col.name]}}</span>
                            </span>
                            <span v-else-if="col.click && prefix==='other-insure' && col.name==='client'">
                                <a v-if="insure.clientId && insure.clientId!==''" @click="col.click(insure, cid)" class="link text-underline">{{insure[col.name]}}</a>
                                <span v-else>{{insure[col.name]}}</span>
                            </span>
                            <span v-else-if="prefix==='other-insure' && col.name==='beneficiary'">
                                <div v-for="(ben, bid) in insure.beneficiary" :key="`${prefix}-col-${bid}-beneficiary`">
                                    <span v-html="ben"></span>
                                </div>
                            </span>
                            <a v-else-if="col.click" @click="col.click(insure, cid)" class="link text-underline">{{insure[col.name]}}</a>
                            <span v-else>{{insure[col.name]}}</span>
                        </td>
                        <td v-if="!isEdit">
                            <a role="button" class="btn btn-icon btn-default btn-sm btn-collapse"
                            v-if="insure.child.length"
                            :class="{'is-open': childStatus.length && childStatus[i] && childStatus[i].show}"
                            @click="toggleChild(insure, i)">
                                <i class="icon-collapse_down"></i>
                            </a>
                        </td>
                        <td v-else>
                            <div class="dropdown">
                              <a :id="`${prefix}-${i}-dropdown`" data-toggle="dropdown" aria-haspopup="true"
                                aria-expanded="false" class="btn btn-icon btn-default btn-sm" role="button">
                                <i class="icon-more"></i>
                              </a>
                              <div class="dropdown-menu" :aria-labelledby="`${prefix}-${i}-dropdown`">
                                <a role="button" @click="$emit('call', 'openInsureForm', 2, true)" class="dropdown-item text-green text-underline">修改</a>
                                <a @click="$emit('call', 'confirmClearInsurance', insure, true)" class="dropdown-item text-green text-underline">刪除</a>
                                <a @click="$emit('call', 'openInsureForm', 6, true)" class="dropdown-item text-green text-underline">新增附約</a>
                              </div>
                            </div>
                        </td>
                    </tr>
                    <tr v-if="insure.child.length" class="child-table"
                    :class="{
                        'show': childStatus.length && childStatus[i].show
                    }">
                        <td :colspan="options.columns.length+1">
                            <table class="table child">
                                <tr v-for="(child, chid) in insure.child" :key="`${prefix}-tr-child-${chid}`">
                                    <td
                                    v-for="(col, cid) in options.columns"
                                    :class="{
                                        'text-right': col.align && col.align === 'right',
                                        'child': cid===0
                                    }"
                                    :style="{
                                        width: `${getClientWidth(i, cid)}px`
                                    }"
                                    :key="`${prefix}-col-${cid}`">     
                                        <span v-if="prefix==='cathay-insure' && col.name==='no'">
                                            {{child.name}}
                                        </span>
                                        <span v-else-if="col.template" v-html="col.template(child)"></span>
                                        <span v-else-if="col.click && prefix==='cathay-insure' && col.name==='target'">
                                            <a v-if="child.target_id && child.target_id!==''" @click="col.click(child, cid)" class="link text-underline">{{child[col.name]}}</a>
                                            <span v-else>{{child[col.name]}}</span>
                                        </span>
                                        <span v-else-if="col.click && prefix==='cathay-insure' && col.name==='client'">
                                            <a v-if="child.clientId && child.clientId!==''" @click="col.click(child, cid)" class="link text-underline">{{child[col.name]}}</a>
                                            <span v-else>{{child[col.name]}}</span>
                                        </span>
                                        <span v-else-if="prefix==='other-insure' && col.name==='beneficiary'">
                                            <div v-for="(ben, bid) in child.beneficiary" :key="`${prefix}-col-${bid}-beneficiary`">
                                                <span v-html="ben"></span>
                                            </div>
                                        </span>
                                        <a v-else-if="col.click" @click="col.click(child, cid)" class="link text-underline">{{child[col.name]}}</a>
                                        <span v-else>{{child[col.name]}}</span>
                                    </td>
                                    <td width="30" v-if="isEdit"></td>
                                    <td width="100" v-else></td>
                                </tr>
                            </table>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { commonMixin } from '../../mixins/common'
import { setTimeout } from 'timers';

export default {
    name: "DataTable",
    mixins: [commonMixin],
    props: {
        collapse: {
            type: Boolean,
            default: false
        },
        editMode: {
            type: Boolean,
            default: false
        },
        color: {
            type: String,
            default: 'green'
        },
        prefix: {
            type: String,
            default: ''
        },
        tableTitle: {
            type: String,
            default: ''
        },
        currentPage:{
            type: Number,
            default: 0
        },
        totalpages:{
            type: Number,
            default: 0
        },
        scrollDataLoading:{
            type: Boolean,
            default: false
        },
        tableData: {
            type: Array,
            default: () => {
                return []
            }
        },
        options: {
            type: Object,
            default: () => {
                return null
            }
        },
        isEdit: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        ...mapGetters(["BaseUrl"]),
        baseLink () {
            return `${window.location.origin}${window.location.pathname}`
        }
    },
    data() {
        return {
            tableList: this.tableData,
            order: {
                index: 0,
                asc: true
            },
            remoteDataMsg: "資料讀取中，請稍候...",
            currentWidth: 0,
            childStatus: []
        }
    },
    watch: {
        "tableData": "initData",
        "collapse": "toggleAll"
    },
    methods: {
        getClientWidth (i, index) {
            if(!this.childStatus.length) return
            
            let $th = this.$el.querySelectorAll("th")
            return $th[index].clientWidth
        },
        initData () {
            this.tableData.forEach((insure, i) => {
                this.childStatus[i] = {
                    render: false,
                    show: false
                }
            })
        },
        toggleAll (val) {
            this.tableData.forEach((insure, i) => {
                if(this.childStatus[i].show !== val) this.toggleChild(insure, i)
            })
        },
        toggleChild (insure, i) {
            this.childStatus[i].show = !this.childStatus[i].show
            this.$forceUpdate()
        },
        columnSorting (i) {
            if(this.order.index===i) {
                this.order.asc = !this.order.asc
            }
            else {
                this.order.index = i
                this.order.asc = true
            }
            this.options.sorting(i + 1)
        }
    },
    mounted () {
        this.initData()
    }
}
</script>

<style lang="scss" scoped>
    tr.child > td {
        padding: 0;
    }
</style>
