                  <a v-if="mem.new" role="button" class="btn btn-sm btn-default"
                  @click="cancelMember(mem, mid)">
                    取消
                  </a>
                  <a v-if="mem.new" role="button" class="btn btn-sm btn-primary"
                  @click="addInsurance(mem, mid)">
                    新增儲存
                  </a>
                  <a v-if="!mem.new" role="button" class="btn btn-sm btn-primary"
                  @click="updateMember(mem, mid)">
                    儲存
