                :key="`beneficiaries-list-${index}`"
                >
                  <td>{{item.prod_id_ch}}({{item.prod_id}})</td>
                  <td>{{item.bene_type_ch}}</td>
                  <td>{{item.bene_name}}</td>
                  <td>{{item.rltn_ch}}</td>
                  <td>{{item.bene_rato}}%</td>
