<script>
import DatePicker from '../../components/DatePicker/components/Datepicker'
import {en, zh} from '../../components/DatePicker/locale'
import { mapActions, mapGetters } from 'vuex';
import { addFamilyRelationOfInsurance } from '../../service'
import { queryFamilyRelationOfInsurance } from '../../service'
import { updateFamilyRelationOfInsurance } from '../../service'
import { deleteFamilyRelationOfInsurance } from '../../service'
import { setTimeout } from 'timers';
import { queryMinorInsurDetail } from '../../service';
import { queryMainPolicyInfo } from '../../service';
import { getInsuDetailPayRecords } from '../../service';
import { queryMainPolicyBeneficiariesInfo } from '../../service';
import { queryPaymentMessage } from '../../service'
import { factory } from 'highcharts/modules/exporting';

export default {
  name: 'InsuranceModal',
  props: {
    open: {
      type: Boolean,
      default: false
    },
    no: {
      type: String,
      default: ''
	  },
    insuranceName: {
      type: String,
      default: ''
    },
    isShowNoIdRelationship: {
      type: Boolean,
      default: false
    }
  },
  components: {DatePicker},
  data () {
    return {
      dateLang: zh,
      dateFormatter: (date) => {
        const m = date.getMonth() + 1
        const d = date.getDate()
        return `${date.getFullYear() - 1911}/${(m < 10 ? '0' : '') + m}/${(d < 10 ? '0' : '') + d}`;
      },
      isLoading_1: false,
      isLoading_2: false,
      isLoading_3: false,
      isLoading_4: false,
      isLoading_5: false,
      insuranceContent: 0,
      roleOpt: ['配偶', '子女'],
      currentIndex: null,
      currentMember: null,
      bufferMember: null,
      familyMembers: [],
      insurDetailList: [], 
      mainPolicyInfo: {},
      payRecordsList: [],
      beneficiariesList: [],
      PaymentDetail: [],
      PaymentRecord: []
    }
  },
  watch: {
    'open': 'initData',
    'currentIndex': function(newValue, oldValue) {
      console.log('watch currentIndex')
      console.log(newValue)
      console.log(oldValue)
      if(this.familyMembers[oldValue].edit == true) {
        
      }
    }
  },
  mounted(){

  },
  computed:{
    ...mapGetters(['UserInfo']),
  },
  methods: {
    initData(val) {
      this.insuranceContent = "0"
      if(val) {
        this.mounteData()
      }
    },
    getFamilyRelationOfInsurance() {
      //查詢保單無記名家庭關係
      queryFamilyRelationOfInsurance(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result => {
        let newDataList = result.body.familymembers
        newDataList.forEach((item,index)=>{
          newDataList[index].edit = false
          newDataList[index].new = false
        })
        
        this.familyMembers = newDataList
        
        // console.log("查詢保單無記名家庭關係 API: "+JSON.stringify(this.familyMembers));
        if(!this.familyMembers) {
          this.familyMembers = []
        }
      })
    },
    getMainPolicyInfo() {
      //查詢主約基本資料
      this.isLoading_2 = true
      queryMainPolicyInfo(this.no, this.$store.state.userInfo.id).then(result => {
        this.mainPolicyInfo = result.body;
        // console.log("查詢主約基本資料 API: "+JSON.stringify(this.mainPolicyInfo));
        if(!this.mainPolicyInfo) {
          this.mainPolicyInfo = {}
        }
        else{
          switch(this.mainPolicyInfo.MED_EXAM_CH){
            case('無體'):
              this.mainPolicyInfo.MED_EXAM_CH = '無體檢'
            break;
          }
        }
        
        this.isLoading_2 = false
      })
    },
    getPaymentMessage() {
      //查詢繳費訊息
      this.isLoading_3 = true
      queryPaymentMessage(this.$store.state.userInfo.id, this.no).then(result =>{
        if(result.body.PaymentDetail){
          this.PaymentDetail = result.body.PaymentDetail
        }
        if(result.body.PaymentRecord){
          this.PaymentRecord = result.body.PaymentRecord
        }
        this.isLoading_3 = false
      })
    },
    getPayRecords() {
      //查詢保全訊息-給付紀錄
      this.isLoading_4 = true
      getInsuDetailPayRecords(this.$store.state.userInfo.id, this.no).then(result => {
        if(result.body.PAY_LIST) {
          this.payRecordsList = result.body.PAY_LIST
        } else {
          this.payRecordsList = []
        }
        this.isLoading_4 = false
      }) 
    },
    getMainPolicyBeneficiariesInfo() {
      //查詢主約受益人明細
      this.isLoading_5 = true
      queryMainPolicyBeneficiariesInfo(this.$store.state.userInfo.id, this.no).then(result => {
        if(result.body.rider_list) {
          this.beneficiariesList = result.body.rider_list
        } else {
          this.beneficiariesList = []
        }
        this.isLoading_5 = false
      })
    },
    toggleEdit(member, i) {
      this.currentIndex = i
      this.currentMember = member
      for(let j=0; j<this.familyMembers.length; j++){
        // reset all edit to false
        this.$set(this.familyMembers[j], ['edit'], false)
      }
      this.$set(this.familyMembers[i], ['edit'], true)
    },
    removeMember (member, index) {
      let cop_id = member.cop_id
      console.log(cop_id)
      deleteQueryCustInsuranceDetail(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, cop_id).then(result =>{
        // console.log("刪除保單無記名家庭關係 API"+result)
      })
      this.currentIndex = null
      this.currentMember = null
      member.edit = false
      this.familyMembers.splice(index, 1)
      
    },
    addInsurance(mem){
      //新增保單無記名家庭關係 API
      this.getChineseDateFormate;
      let cop_id = mem.cop_id
      let name = mem.cop_name
      let birthday = this.currentMember.cop_birthday.toLocaleDateString()
      let kind = mem.cop_kind
      addQueryCustInsuranceDetail(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, cop_id, name, birthday, kind).then(result => {
        // console.log("新增保單無記名家庭關係 API: "+result);
      })
      this.currentMember = []
      setTimeout(
        this.mounteData()
      ,500)
      // console.log("mounteData");
    },
    addFamily () {
      if(this.currentIndex !== null) {
        this.familyMembers[this.currentIndex].edit = false
        this.currentMember = null
      }
      this.familyMembers.push({
        cop_name: '',
        cop_kind: '',
        id: '',
        cop_birthday: '',
        edit: false,
        new: true
      })

      this.familyMembers[this.familyMembers.length - 1].edit = true
      this.currentMember = this.familyMembers[this.familyMembers.length - 1]
    },
    getInsurance () {
      this.isLoading_1 = true
      queryMinorInsurDetail(this.UserInfo.id, this.no).then(result => {
        if(result.body.rider_list && result.body.rider_list.length > 0) {
          this.insurDetailList = result.body.rider_list.map(item => {
            let rdAmtUnit = ''
            switch(item.rd_amt_unit) {
              case '1':
                rdAmtUnit = '元'
                break;
              case '2':
                rdAmtUnit = '百'
                break;
              case '3':
                rdAmtUnit = '千'
                break;
              case '4':
                rdAmtUnit = '萬'
                break;
              case '7':
                rdAmtUnit = '單位'
                break;
              case '8':
                rdAmtUnit = '計畫別'
                break;
              default:
                rdAmtUnit = ''
            }
            return {
              policy_no: item.policy_no,
              cs_name: item.cs_name,
              cs_id: item.id,
              eft_code_ch: item.eft_code_ch,
              term_date: item.term_date,
              rd_name: item.rd_name,
              rd_id: item.rd_id,
              rd_prem: Number(item.rd_prem),
              rd_prem_unit: item.rd_prem_unit,
              rd_amt: Number(item.rd_amt),
              rdAmtUnit: rdAmtUnit,
              curr: item.curr,
              product_link: item.product_link
            }
          })
          this.isLoading_1 = false
        } else {
          this.insurDetailList = []
          this.isLoading_1 = false
        }
      })
    },
    mounteData(){
      if(this.isShowNoIdRelationship) {
        this.getFamilyRelationOfInsurance()
      }
      this.getMainPolicyInfo()
      this.getPaymentMessage()
      this.getPayRecords()
      this.getInsurance()
    },
    toggleEdit(member, i) {
      let checkEdit = false
      this.familyMembers.forEach(item=>{
        if(item.edit){
          checkEdit = true
        }
      })
      if(checkEdit){   //如果有欄位是編輯中的話
        this.$toast.info({title: '有欄位尚未儲存'})
      }else{
        this.familyMembers[i].edit = true
      }
    },
    removeMember (member, index) {
      let cop_id = member.cop_id
      deleteFamilyRelationOfInsurance(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, cop_id).then(result =>{
        if(result.header.status && result.header.status == '200') {
          this.$toast.info({ title: "「無記名家庭關係」已刪除" });
          this.getFamilyRelationOfInsurance()
        } else {
          this.$toast.info({ title: "「無記名家庭關係」刪除失敗" });
        }
      })
    },
    cancelMember(member, index) {
      this.familyMembers.pop()
    },
    getChineseDateFormate(cop_birthday) {
      if(cop_birthday !== null && cop_birthday !== ''){
        let dt = window.moment(cop_birthday)
        let m = dt.month() + 1 < 10 ? `0${dt.month() + 1}` : dt.month() + 1
        let d = dt.date()  < 10 ? `0${dt.date()}` : dt.date()
        return `${dt.year() - 1911}/${m}/${d}`
      }else{
        return ''
      }
    },
    addInsurance(mem){
      //新增保單無記名家庭關係 API
      // this.getChineseDateFormate;
      let cop_id = mem.cop_id
      let name = mem.cop_name
      let dt = mem.cop_birthday
      let dm = ((dt.getMonth() + 1) < 10 ? "0" : "") + (dt.getMonth() + 1)
      let dd = (dt.getDate() < 10 ? "0" : "") + dt.getDate()
      let birthday = `${dt.getFullYear() - 1911}${dm}${dd}`  //等民國生日套件完成再修改
      let kind = mem.cop_kind
      let idRegex = /^[A-Z]{1}[0-9]{9}$/
      if (idRegex.test(cop_id)){  //驗證ID 格式 以及不能為空值
        addFamilyRelationOfInsurance(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, cop_id, name, birthday, kind).then(result => {
          // console.log("新增保單無記名家庭關係 API: "+result);
          if(result.header.status && result.header.status == '200') {
            this.$toast.info({ title: "「無記名家庭關係」已新增" });
            this.getFamilyRelationOfInsurance()
          } else {
            this.$toast.info({ title: "「無記名家庭關係」新增失敗" });
          }
        })
      }else{
        this.$toast.info({ title: "ID格式不符 或 資料欄位不得為空" });
      }
      
    },
    updateMember (member) {
      var birthday;
      if(typeof member.cop_birthday=="object") {
        let dt = member.cop_birthday
        let dm = ((dt.getMonth() + 1) < 10 ? "0" : "") + (dt.getMonth() + 1)
        let dd = (dt.getDate() < 10 ? "0" : "") + dt.getDate()
        birthday = `${dt.getFullYear() - 1911}${dm}${dd}`  //等民國生日套件完成再修改
      } else {
        birthday = member.cop_birthday
      }
      let cop_id = member.cop_id
      let name = member.cop_name
      let kind = member.cop_kind
      // console.log({cop_id, name, birthday, kind});
      updateFamilyRelationOfInsurance(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, cop_id, name, birthday, kind).then(result => {
        if(result.header.status && result.header.status == '200') {
          this.$toast.info({ title: "「無記名家庭關係」已更新" });
          this.getFamilyRelationOfInsurance()
        } else {
          this.$toast.info({ title: "「無記名家庭關係」更新失敗" });
        }
      })
    },
    addFamily () {
      let checkEdit = false
      this.familyMembers.forEach(item=>{
        if(item.edit){
          checkEdit = true
        }
      })
      if(checkEdit){   //如果有欄位是編輯中的話
        this.$toast.info({title: '有欄位尚未儲存'})
      }else{
        this.familyMembers.push({
          agidno: this.$store.state.userInfo.id,
          cop_name: '',
          cop_kind: '',
          cop_id:'',
          id: this.$store.state.custInfo.ID,
          cop_birthday: '',
          edit: true,
          new: true,
        })
      }
    }
  }
}
</script>

